* {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  box-sizing: border-box;
}

:root {
  /* Primary color */
  --Bright-Blue: hsl(220, 98%, 61%);
  --Check-Background: linear-gradient(
    to bottom,
    hsl(192, 100%, 67%),
    hsl(280, 87%, 65%)
  );
  /* Light theme */
  --Very-Light-Gray: hsl(0, 0%, 98%);
  --Very-Light-Grayish-Blue: hsl(236, 33%, 92%);
  --Light-Grayish-Blue: hsl(233, 11%, 84%);
  --Dark-Grayish-Blue: hsl(236, 9%, 61%);
  --Very-Dark-Grayish-Blue: hsl(235, 19%, 35%);
  /* Dark theme */
  --Very-Dark-Blue: hsl(235, 21%, 11%);
  --Very-Dark-Desaturated-Blue: hsl(235, 24%, 19%);
  --Light-Grayish-Blue: hsl(234, 39%, 85%);
  --Light-Grayish-Blue-hover: hsl(236, 33%, 92%);
  --Dark-Grayish-Blue: hsl(234, 11%, 52%);
  --Very-Dark-Grayish-Blue: hsl(233, 14%, 35%);
  --Ultra-Dark-Grayish-Blue: hsl(237, 14%, 26%);
  /* Font size */
  --fs: 18px;
  /* Font weight */
  --fw-400: 400;
  --fw-700: 700;
}

.app-component {
  min-height: fit-content;
  position: relative;
}

/* POSITION */
.todo-absolute {
  position: absolute;
  top: 3rem;
  left: 50%;
  transform: translateX(-50%);
  width: 95vw;
  max-width: 30rem;
  padding: 10px 10px 50px 10px;
}

#todo-output {
  margin-top: 1.5rem;
  border-radius: 5px;
  box-shadow: 0 15px 20px -8px rgba(0, 0, 0, 0.349);
}

.todo-output {
  position: relative;
  padding-bottom: 4rem;
}

/* PADDING */
.todo-header-padding {
  padding-bottom: 30px;
}

/* CHECK BUTTON */
#check-btn {
  height: 1.3rem;
  width: 1.3rem;
  margin: auto 1rem;
}

.btn-filter {
  color: var(--Very-Dark-Grayish-Blue);
  background: none;
  padding: 0 5px;
  border: none;
  font-weight: 700;
}

.btn-filter:active,
.btn-filter:focus,
.btn-filter:hover {
  color: var(--Bright-Blue);
}

/* INPUT AND OUTPUT CONTAINER */
.input-container {
  height: 2.5rem;
  width: 100%;
  border-radius: 5px;
}

.output-container {
  color: var(--Very-Dark-Grayish-Blue);
  width: 100%;
  border-radius: 5px;
  padding-right: 20px;
  min-height: 2.5rem;
  height: fit-content;
}

.output-container h3 {
  width: 100%;
  overflow-wrap: break-word;
  padding-block: 10px;
  color: var(--Dark-Grayish-Blue);
}

/* RADIO INPUT */
.radio-label {
  background: transparent;
  width: 1.5rem;
  height: 1.5rem;
  border: 1px solid var(--Very-Dark-Grayish-Blue);
  border-radius: 50%;
  margin: auto 10px;
}

input[type="radio"],
input[type="checkbox"] {
  display: none;
}

input[type="checkbox"] + label {
  position: relative;
}

input[type="checkbox"]:checked + label {
  border: none;
}

input[type="checkbox"]:checked + label::before {
  content: "";
  position: absolute;
  background: var(--Check-Background);
  background-position: 50% 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 50%;
}

input[type="checkbox"]:checked + label::after {
  content: "";
  position: absolute;

  background-image: url("../src/images/icon-check.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  z-index: 1000;
}

/* BACKGROUND COLOR */
.bg-very-dark-blue {
  background-color: var(--Very-Dark-Blue);
}

.bg-gray {
  background-color: var(--Very-Dark-Desaturated-Blue);
}

/* BACKGROUND CHANGING  BUTTON ICON */
.change-icon {
  background: none;
  border: none;
}

/* font-family */
.ff-default {
  font-family: Arial, Helvetica, sans-serif;
  color: var(--Very-Light-Gray);
}

.input-dark-mode,
.output-dark-mode {
  border: none;
  width: 100%;
  /* height: 2rem; */
  outline: none;
  border-radius: 3px;
  color: var(--Light-Grayish-Blue);
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.items-left {
  padding-left: 10px;
}

.filters-sm {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
  display: flex;
  justify-content: space-evenly;
  border-radius: 5px;
  padding-inline: 10px;
}
